import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);


export const store = new Vuex.Store({
	state: {
		amount: 1,
		currency: {
			rate: 'RUB',
			title: 'Российский рубль'
		},
		to: {
			rate: 'RUB',
			title: 'Российский рубль'
		},
		from: {
			rate: 'USD',
			title: 'Доллар США'
		},
		favorites: []
	},
	getters: {
		currency(state){
			return state.currency;
		},
		from(state) {
			return state.from;
		},
		favorites(state) {
			return state.favorites;
		},
		amount(state) {
			return state.amount;
		}
	},
	mutations: {
		set(state, {type, data}){
			state[type] = {
				rate: data.rate,
				title: data.title
			};
		},
		favorites(state, {type, data}){
			state[type].push({
				amount: data.amount,
				ex: data.ex,
				from: data.from,
				to: data.to
			});
		},
		deleteFavorites(state, {type, data}){
			state[type].splice(data.index, 1);
		},
		changeAmount(state, {type, data}){
			state[type] = data;
		},
		changeCurrency(state){
			const temp = state['to'];
			state['to'] = state['from'];
			state['from'] = temp;
		}

	},
	actions: {
		changeRate({ commit }, items){
			commit('set', {type: items.type, data: items})
		},
		changeFavorites({ commit }, item){
			commit('favorites', {type: 'favorites', data: item})
		},
		deleteFavorites({commit}, item){
			commit('deleteFavorites', {type: 'favorites', data: item});
		},
		changeAmount({commit}, item){
			commit('changeAmount', {type: 'amount', data: item});
		},
		changeCurrency({commit}){
			commit('changeCurrency');
		}
		
	}
});
