<template>
	<div class="wrapper">
		<div class="row">
			<div class="col">
				<input type="text" id="amount" placeholder="<>" v-model="amount" @keyup="onChange($event)">
			</div>
			<div class="col">
				<div class="choose-currency">
					<button class="btn" @click="show = !show" id="target"><img id="target" :src="require('@/assets/flags/' + from.rate + '.png')" alt="$" class="flag">{{from.rate}} - {{from.title}}</button>
					<transition name="fade">
						<ul v-if="show" class="list-currency">
							<li v-for="(item, index) in ratesFrom" :key="index" @click="chooseFrom(index, item.title)">
								<img :src="require('@/assets/flags/' + index + '.png')" alt="$" class="flag">
								<span class="link-currency">{{item.title}}</span>
							</li>
						</ul>
					</transition>
				</div>
			</div>
			<div class="col turn-over"><svg class="exchange-alt" @click="changeCurrency()" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M0 168v-16c0-13.255 10.745-24 24-24h360V80c0-21.367 25.899-32.042 40.971-16.971l80 80c9.372 9.373 9.372 24.569 0 33.941l-80 80C409.956 271.982 384 261.456 384 240v-48H24c-13.255 0-24-10.745-24-24zm488 152H128v-48c0-21.314-25.862-32.08-40.971-16.971l-80 80c-9.372 9.373-9.372 24.569 0 33.941l80 80C102.057 463.997 128 453.437 128 432v-48h360c13.255 0 24-10.745 24-24v-16c0-13.255-10.745-24-24-24z"></path></svg></div>
			<div class="col">
				<div class="choose-currency">
					<button class="btn" @click="showTwo = !showTwo" id="targetTwo"><img id="targetTwo" :src="require('@/assets/flags/' + to.rate + '.png')" alt="$" class="flag">{{to.rate}} - {{to.title}}</button>
					<transition name="fade">
						<ul v-if="showTwo" class="list-currency">
							<li v-for="(item, index) in ratesTo" :key="index" @click="chooseTo(index, item.title)">
								<img :src="require('@/assets/flags/' + index + '.png')" alt="$" class="flag">
								<span class="link-currency">{{item.title}}</span>
							</li>
						</ul>
					</transition>
				</div>
			</div>
		</div>
		<div class="converter-result">
			<div class="result-inner">
				<div class="converter-result-from"><span class="result-amount">{{amount}}</span> {{from.rate}} = </div>
				<div class="converter-result-to"><span class="result-amount">{{ex}}</span> {{to.rate}}</div>
			</div>
			<span class="add-favorite" v-on:click="addFavorites()">Добавить в закладки</span>
		</div>
	</div>
</template>

<script>

import services from '../services';
import {eventEmitter} from './../main';
import store from './../store';

export default {
	name: 'Converter',
	data(){
		return {
			amount: 1,
			ratesTo: {
				rate: 'RUB',
				title: 'Российский рубль'
			},
			ratesFrom: {
				rate: 'USD',
				title: 'Доллар США'
			},
			to: {},
			from: {},
			stores: {},
			show: false,
			showTwo: false,
			ex: ''
		}
	},
	methods: {
		target(e) {
			if( e.target.id != 'target'){
				this.show = false;
			}
			if( e.target.id != 'targetTwo'){
				this.showTwo = false;
			}
		},
		async chooseFrom(cur, title){
			this.from = {
				rate: cur,
				title: title
			}

			this.$store.dispatch('changeRate', {
				type: 'from',
				rate: cur,
				title: title
			});
	
			const exchange2 = await services.getRates(this.from.rate);
			this.ratesFrom = exchange2;

			this.calculation();

		},
		async chooseTo(cur, title){
			this.to = {
				rate: cur,
				title: title
			}
			this.$store.dispatch('changeRate', {
				type: 'to',
				rate: cur,
				title: title
			});
			const exchange = await services.getRates(this.to.rate);
			this.ratesTo = exchange;

			this.calculation();

		},
		async onChange(event){

			this.amount = event.target.value;

			this.$store.dispatch('changeAmount', this.amount);

			this.calculation();

			this.initRates();

		},
		addFavorites(){
			this.$store.dispatch('changeFavorites', {
				type: 'favorites',
				amount: this.amount,
				ex: this.ex,
				from: this.from,
				to: this.to
			});
		},
		async changeCurrency(){

			this.$store.dispatch('changeCurrency');

			this.calculation();

			this.initRates();

		},
		fixed(arg){
			return arg.toFixed(2)
		},
		async initRates(){
			const exchange = await services.getRates(this.to.rate);
			this.ratesTo = exchange;

			const exchange2 = await services.getRates(this.from.rate);
			this.ratesFrom = exchange2;
		},

		async calculation(){

			this.to = this.$store.state.to;
			this.from = this.$store.state.from;

			this.amount= this.$store.getters.amount;

			const calculate = await services.calculate(this.from.rate, this.to.rate);

			this.ex = this.fixed(calculate*this.amount);

			isNaN(this.ex) ? this.ex = this.amount  : this.ex;

		}

	},
	async created() {

		this.calculation();

		this.initRates();

		document.addEventListener('click', this.target);

	}
}
</script>

<style scoped>

	div.row {
		display: flex;
		margin-bottom: 20px;
		justify-content: space-between;
	}

	.converter-result {
		display: flex;
		justify-content: space-between;
	}

	.result-inner {
		background-color: #f9f9f9;
		width: 70%;
		display: flex;
		flex-flow: column;
		align-items: center;
		flex-grow: 5;
		padding: 20px 0;
	}

	.add-favorite {
		cursor: pointer;
		border: 1px solid rgb(231, 231, 231);
		display: flex;
		width: 30%;
		font-size: 14px;
		align-items: center;
		justify-content: center;
		text-align: center;
	}

	.converter-result-from, .converter-result-to{
		width: calc(100% - 10px);
		display: block;
		text-align: center;
		word-wrap: break-word;
		word-break: break-word;
		padding: 5px;
		margin-bottom: 10px;
	}


	.result-amount {
		font-size: 32px;
	}

	div.wrapper {
		margin-top: 35px;
		width: 100%;
		display: flex;
		flex-flow: column;
	}

	div.col {
		display: flex;
		flex-flow: column;
		justify-content: center;
	}

	.col:first-child {
		width: 12%;
	}

	.choose-currency {
		position: relative;
	}

	.list-currency {
		padding: 0;
		margin: 0;
		border: 1px solid #999;
		border-radius: 2%;
		background: #fff;
		list-style: none;
		position: absolute;
		text-align: left;
		z-index: 999;
		overflow-y: auto;
		height: 200px;
	}

	.list-currency li {
		display: flex;
		align-items: center;
		cursor: pointer;
		padding: 5px 7px;
	}

	.list-currency li:hover {
		background: rgb(247, 247, 247);
	}

	.list-currency li:first-child:hover{
		border-top-left-radius: 5px;
		border-top-right-radius: 5px;
	}

	.list-currency li:last-child:hover{
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
	}


	.fade-enter-active, .fade-leave-active {
	  transition: opacity .2s;
	}

	.fade-enter, .fade-leave-to {
	  opacity: 0;
	}

	.btn {
		display: flex;
		align-items: center;
		outline: none;
		cursor: pointer;
		width: 255px;
		padding: 4px 20px;
		background: #fff;
		border: 1px solid rgb(231, 231, 231);
		border-radius: 4%;
	}

	.convert-result {
		display: flex;
	}

	#amount {
		font-size: 20px;
		outline: none;
		height: 100%;
		padding: 4px 20px;
		text-align: center;
		border: 1px solid rgb(231, 231, 231);
		box-shadow: 0 0 0px 1000px white inset;
	}

	.exchange-alt {
		cursor: pointer;
		width: 20px;
		height: 20px;
		fill: #4caf50;
	}

	.flag {
		height: 30px;
		margin-right: 10px;
	}


/* MEDIA */
	@media(max-width: 690px){
		div.row {
			flex-flow: column;
		}
		.btn {
			width: 100%;
		}
		.col:first-child {
			width: 100%;
		}

		.col {
			width: 100%;
			margin-top: 20px;
		}

		.turn-over {
			flex-flow: row;
			align-items: center;
		}

		.list-currency {
			width: 100%;
		}

		.choose-currency {
			width: 100%;
		}

		
	}

</style>
