import Vue from 'vue';
import Currencies from './views/сurrencies.vue';
import Converter from './views/converter.vue';
// import posts from './components/postComponent.vue';
import VueRouter from 'vue-router';


Vue.use(VueRouter);


export default new VueRouter({
    mode: 'history',
    routes: [{
        path: '/app01/',
        name: 'Converter',
        component: Converter
    },
    {
        path: '/app01/сurrencies',
        name: 'Currencies',
        component: Currencies
    }]
})