<template>
	<header>
		<transition-group name="slide-fade" tag="div" class="favorites">
				<li v-for="(item, index) in favorites" v-bind:key="'favorites_' + index">
					<span class="text-favorite">{{item.amount}} {{item.from.rate}} = {{item.ex}} {{item.to.rate}}</span> 
					<span class="btn-delete" @click="onDelete(index)">x</span>
				</li>
				<span v-if="favorites.length == 0" :key="'notfound'" class="notfound-favorites">Закладки</span>
		</transition-group>
		<nav>
			<router-link to="/app01/" exact>Конвертер</router-link>
			<router-link to="/app01/сurrencies" exact>Валюты</router-link>
		</nav>
	</header>
</template>

<script>

import store from '../store';

export default {
	name: 'Header',
	data(){
		return {
			favorites: []
		}
	},
	methods: {
		onDelete(index){
			this.$store.dispatch('deleteFavorites', {
				type: 'favorites',
				index: index
			});
		}
	},
	computed: {
		update(){
			this.favorites = this.$store.getters.favorites;
		}
		
	},
	watch: {
		update(){
			this.favorites = this.$store.getters.favorites;
		}
	}

}

</script>


<style scoped>
	header {
		width: 100%;
	}

	nav a {
		color: #000;
		text-decoration: none;
		margin-right: 20px;

	}

	.favorites {
		display: flex;
		overflow-y: auto;
		background-color: #f9f9f9;
		height: 123px;
		margin-bottom: 20px;
		flex-wrap: wrap;
		list-style: none;
	}

	.favorites li {
		display: flex;
		/* height: 25px; */
		background: #fff;
		border: 1px solid rgb(231, 231, 231);
		border-radius: 4%;
		margin: 5px;

	}

	.btn-delete {
		display: flex;
		align-items: center;
		cursor: pointer;
		text-align: center;
		height: 100%;
		background-color:  tomato;
		color: #fff;
		padding: 0 8px;
	}

	.text-favorite {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 7px 10px 4px 10px;
		word-break: break-word;
	}


	.slide-fade-enter-active {
	  transition: all .2s ease;
	}


	.slide-fade-enter, .slide-fade-leave-to{
	  opacity: 0;
	}

	.notfound-favorites {
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
	}

	.router-link-active {
		color: #4caf50;
	}


/* MEDIA */

	@media(max-width: 690px){
		.favorites li {
			width: 100%;
			justify-content: space-between;
		}
	}

</style>

