import axios from 'axios';
import json from './assets/data';

class services {
	static getRates(rate){
		return new Promise(async(resolve, reject)=>{
			try {
				const res = await axios.get('https://ratesapi.io/api/latest?base='+rate);
				const data = res.data;
				let array = {};

				for(const code in data.rates){
					for(const item in json){
						if(item == code){
							array[code]= {
								title: json[item],
								rate: data.rates[code]
							};
						}
					}
				}
				resolve(array);
			}catch(err){
				reject(err)
			}
		});
	}
	static calculate(from, to){
		return new Promise(async(resolve, reject)=>{
			try{
				const res = await axios.get('https://ratesapi.io/api/latest?base='+from);
				const data = res.data;
				resolve(data.rates[to]);
			}catch(err){
				reject(err);
			}
		});
	}
}

export default services;