<template>
	<div class="wrapper">
		<div class="tools">
			<div class="choose-currency">
				<button class="btn" v-on:click="show = !show" id="target"><img id="target" :src="require('@/assets/flags/' + stores.rate + '.png')" alt="$" class="flag">{{stores.rate}} - {{stores.title}}</button>
				<transition name="fade">
					<ul v-if="show" class="list-currency">
						<li v-for="(item, index) in rates" v-bind:key="index" v-on:click="choose(index, item.title)">
							<img :src="require('@/assets/flags/' + index + '.png')" alt="$" class="flag">
							<span class="link-currency">{{item.title}}</span>
						</li>
					</ul>
				</transition>
			</div>
		</div>
		<ul class="rates">
			<li><span class="code">Код</span><span class="currency">Валюта</span><span class="rate">Курс</span></li>
			<li v-for="(item, index) in rates" v-bind:key="index">
				<span class="code">{{index}}</span>
				<span class="currency">{{item.title}}</span>
				<span class="rate">{{item.rate}}</span>
			</li>
		</ul>
	</div>
</template>

<script>

import services from '../services';
import header from './../components/header';
import store from './../store';

export default {
	name: 'Currencies',
	data() {
		return {
			stores: {},
			rates: {},
			show: false
		}
	},
	methods: {
		target(e) {
			if( e.target.id != 'target'){
				this.show = false;
			}
		},
		async choose(cur, title){
			this.stores = {
				rate: cur,
				title: title
			}

			this.$store.dispatch('changeRate', {
				type: 'currency',
				rate: cur,
				title: title
			});

			const exchange = await services.getRates(this.stores.rate);
			this.rates = exchange;
		}

	},
	async created() {
		this.stores = this.$store.getters.currency;
		const exchange = await services.getRates(this.stores.rate);
		this.rates = exchange;
		document.addEventListener('click', this.target);
	}


}
</script>

<style scoped>

	input {
		position: relative;
	}

	.wrapper {
		width: 100%;
		padding-top: 35px;
		font-family: 'PT Sans', sans-serif;
	}

	ul.rates {
		width: 100%;
		display: flex;
		flex-flow: column;
		list-style: none;
		margin: 0;
		padding: 0;
		margin-top: 30px;
	}

	ul.rates li:first-child{
		background: #4caf50;
		color: #fff;
		padding: 9px 10px;
	}

	.rates li {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
		padding: 2px 10px;
	}

	span.rate {
		text-align: right;
	}

	span.code {
		text-align: left;
	}

	.btn {
		display: flex;
		align-items: center;
		outline: none;
		cursor: pointer;
		padding: 4px 20px;
		background: #fff;
		border: 1px solid rgb(231, 231, 231);
		border-radius: 4%;
	}

	.rates li span {
		display: block;
		width: 33%;
	}

	span.currency {
		text-align: left;
	}

	.choose-currency {
		position: relative;
	}

	.list-currency {
		padding: 0;
		margin: 0;
		border: 1px solid #999;
		border-radius: 2%;
		background: #fff;
		list-style: none;
		position: absolute;
		text-align: left;
		z-index: 999;
		overflow-y: auto;
		height: 200px;
	}

	.list-currency li {
		display: flex;
		align-items: center;
		cursor: pointer;
		padding: 5px 7px;
		
	}

	.list-currency li:hover {
		
		background: rgb(247, 247, 247);
	}

	div.tools {
		display: flex;
		width: 100%;
	}

	.fade-enter-active, .fade-leave-active {
	  transition: opacity .2s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
	  opacity: 0;
	}

	.flag {
		height: 30px;
		margin-right: 10px;
	}

	.list-currency li:first-child:hover{
		border-top-left-radius: 5px;
		border-top-right-radius: 5px;
	}

	.list-currency li:last-child:hover{
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
	}


/* MEDIA */
	@media(max-width: 690px){

		.btn {
			width: 100%;
		}

		.rates .rate{
			width: 30%;
		}
		.rates .currency{
			width: 50%;
		}

		.rates .code{
			width: 20%;
		}
		.list-currency {
			width: 100%;
		}

		.choose-currency {
			width: 100%;
		}

	}
</style>

